/* MAIN FORM */
.form-container {
    background: #fff;
    max-width: 598px;
    margin: 32px auto;
    box-shadow: 0 1px 2px 0 rgb(60 64 67 / 30%), 0 2px 6px 2px rgb(60 64 67 / 15%);
    border: 1px solid #e0e1e0;
    border-radius: 8px;
  }
  .form-container form {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    color: #5f6368;
  }
  .note-title {
    border: none;
    font-size: 1rem;
    height: 100%;
    padding: 10px;
    margin-left: 10px;
    width: 100%;
    margin-right: 10px;
    padding-left: 10px;
  }
  .note-text {
    border: none;
    font-size: 1rem;
    height: 100%;
    padding: 10px;
    margin-left: 10px;
    width: 100%;
    margin-right: 10px;
    padding-left: 10px;
  }
  .form-actions {
    display: flex;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 10px;
  }
  
  .form .form-actions .icons {
    display: flex;
  }
  .form .form-actions {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-top: 5px;
  }
  .close-btn {
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1rem;
  }
  .small-icon {
    color: black;
    font-size: 1.1rem;
  }